@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primaryColor: #5a189a;
  --secondaryColor: #ff9e00;
  --darkAccentColor: #240046;
  --lightAccentColor: #ff8500;
  --foreffectColor: rgb(73, 51, 99);
  --mainBlack: #222;
  --mainWhite: #fdfdfd;
  --mainTransition: cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

*, ::after, ::before {
	box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
  font-size: 100%;
}

body {
  display: block;
  overflow-x: hidden;
}

::-webkit-scrollbar {
	display: none;
}

.app {
  background: linear-gradient(to right, #240046, #10002b);
  background: linear-gradient(to right, var(--darkAccentColor), #10002b);
  background-size: 120%;
  background-position: center;
  background-attachment: fixed;
  background-blend-mode: multiply;
	color: #fdfdfd;
  font-family: 'Raleway', sans-serif;
}

a,
a:active,
a:visited {
	color: #fdfdfd;
	color: var(--mainWhite);
  text-decoration: none;
  transition: all 0.3s cubic-bezier(0.455, 0.03, 0.515, 0.955);
  transition: all 0.3s var(--mainTransition);
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

a:hover {
  color: #fdfdfd;
  color: var(--mainWhite);
  text-decoration: none;
  letter-spacing: 0.1rem;
}

.hero-underline {
	margin: 0.5rem 0 3rem 0.7rem;
}

.hero-underline span {
	width: 6.25rem;
	height: 4px;
	border-radius: 5px;
	background-color: #ff9e00;
	background-color: var(--secondaryColor);
	display: block;
  margin-bottom: 1rem;
  transition: 0.5s all cubic-bezier(0.455, 0.03, 0.515, 0.955);
  transition: 0.5s all var(--mainTransition);
}

.hero-underline #second-underline {
	margin-left: 4.5rem;
}

.hero-underline:hover span {
  margin-left: 4.5rem;
}

.hero-underline:hover  #second-underline {
  margin-left: 0;
}

ul, li {
  list-style-type: none;
  padding: 0;
}

.section-head {
  display: flex;
	align-items: center;
  padding: 3rem 0;
}

.section-head h1 {
	font-size: 8rem;
	letter-spacing: 1rem;
	font-weight: 700;
	text-transform: uppercase;
	opacity: 0.2;
  margin: 0;
}

.section-head span {
	font-size: 2rem;
	margin-left: -10%;
  border-bottom: 0.25rem solid #ff9e00;
  border-bottom: 0.25rem solid var(--secondaryColor);
}

#go-back {
  font-size: 2rem;
  position: fixed;
  top: 15%;
  left: 2.5%;
  cursor: pointer;
  z-index: 999;
  transition: 0.3s all cubic-bezier(0.455, 0.03, 0.515, 0.955);
  transition: 0.3s all var(--mainTransition);
}

#go-back:hover {
  transform: scale(1.1);
  color: #ff9e00;
  color: var(--secondaryColor);
}

@media only screen and (max-width: 1000px) {
  html {
    font-size: 70%;
  }

  body:not(img) {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
}

@media only screen and (max-width: 600px) {
  html {
    font-size: 50%;
  }

  .hero {
    height: 100vh;
    margin-top: 69px;
  }

  .section-head {
    padding: 3rem 0;
  }
  
  .section-head h1 {
    font-size: 6rem;
    letter-spacing: 0.75rem;
    font-weight: 700;
    text-transform: uppercase;
    opacity: 0.2;
    margin: 0;
  }
  
  .section-head span {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    font-size: 2rem;
    margin-top: 10%;
    margin-left: -35%;
  }

  #go-back {
    display: none;
  }
}
.about {
  padding: 1% 13%;
  margin: 5rem 0;
}

.about-content {
  display: flex;
  justify-content: space-around;
}

.about-content-para {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  font-size: 1.5rem;
  line-height: 2.3rem;
  margin: 24px 0;
}

.about-skills {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  margin: 2rem 0;
  text-align: center;
  align-items: flex-start;
}

.about-skills div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 5rem;
  transition: 0.4s all var(--mainTransition);
  color: rgba(255, 255, 255, 0.6);
  margin: 0 0 3rem 3rem;
}

.about-skills div span {
  font-size: 0.75rem;
  position: relative;
  margin-top: -30px;
  opacity: 0;
  transition: all 0.4s var(--mainTransition);
}

.about-skills div:hover span {
  margin-top: 10px;
  opacity: 1;
  color: var(--mainWhite) !important;
}

.about-skills div:nth-child(1):hover {
  color: #f0db4f;
}
.about-skills div:nth-child(2):hover {
  color: #61dbfb ;
}
.about-skills div:nth-child(3):hover {
  color: #3C873A;
}
.about-skills div:nth-child(4):hover {
  color: #4DB33D;
}
.about-skills div:nth-child(5):hover {
  color: #f34f29;
}
.about-skills div:nth-child(6):hover {
  color: #2965f1;
}
.about-skills div:nth-child(7):hover {
  color: #e04a34;
}
.about-skills div:nth-child(8):hover {
  color: #fbbb4d;
}
.about-skills div:nth-child(9):hover {
  color: #313564;
}
.about-skills div:nth-child(10):hover {
  color: #0078d7;
}
.about-skills div:nth-child(11):hover {
  color: var(--mainWhite);
}
.about-skills div:nth-child(12):hover {
  color: #0078d7;
}

.image-wrapper {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  position: relative;
  display: block;
}

.image-wrapper img {
  width: 25rem;
  position: relative;
  z-index: 10;
}

.image-wrapper::after {
  border: 0.22rem solid #ff9e00;
  top: 15px;
  left: 15px;
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  content: " ";
  z-index: 0;
  transition: all 0.4s var(--mainTransition);
}

.image-wrapper:hover::after {
  border: 3px solid var(--mainWhite);
  top: 10px;
  left: 10px;
}

.hero-image {
  position: relative;
  margin: auto 8%;
}

.aboutdesc-div {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	padding: 8rem 0;
}

.aboutdesc-div-title {
	position: absolute;
	width: 100%;
	left: 0;
	padding-left: 13%;
}

.aboutdesc-div-title h3 {
	font-size: 4rem;
	font-weight: 500;
	margin: auto;
	color: #ff9e00;
	display: flex;
	align-items: center;
}

.aboutdesc-div-title h3::after {
	content: " ";
	width: 11%;
	height: 2px;
	background-color: var(--mainWhite);
	position: absolute;
	left: 0;
	margin: auto;
}

.aboutdesc-div-title h2 {
	position: relative;
	font-size: 5rem;
	font-weight: 700;
	letter-spacing: 0.5rem;
	z-index: 10;
	margin: 0 0 2rem;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
	word-wrap: break-word;
}

.aboutdesc-div-content {
  display: block;
	max-width: 40%;
	position: relative;
	margin: 0 10% 0 50%;
	transition: all 0.5s var(--mainTransition);
}

.aboutdesc-div-content ul {
  list-style-type: none;
  padding: 0;
}

.aboutdesc-div-content ul li,
.aboutdesc-div-content {
  line-height: 3rem;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
}

.aboutdesc-div-content span {
  color: #ff9e00;
}

@media only screen and (max-width: 1000px) {
  .about {
    padding: 2% 10%;
  }

  .about-content-para {
    width: 300px;
  }

  .aboutdesc-div-content {
    display: block;
    max-width: 100%;
    position: relative;
    margin: 18rem 10rem 0rem;
    transition: all 0.5s var(--mainTransition);
  }

  .aboutdesc-div {
    padding: 5rem 0;
  }
}

@media only screen and (max-width: 600px) {
  .about {
    padding: 2% 7%;
    padding-top: 8rem;
  }

  .about-content {
    flex-direction: column-reverse;
    justify-content: space-around;
  }

  .about-content-para {
    margin: 24px auto;
  }

  .image-wrapper {
    margin: 0 auto;
  }

  .image-wrapper img {
    width: 300px;
  }

  .aboutdesc-div-content {
    display: block;
    max-width: 100%;
    position: relative;
    margin: 20rem 7rem 0rem;
    transition: all 0.5s var(--mainTransition);
  }
}
.contact-home {
  padding: 0 13%;
  margin-bottom: 5rem;
}

#contact-form {
  position: fixed;
  right: 0;
  top: 0;
  height: 100vh;
  width: 37vw;
  margin-right: -100vw;
  z-index: 1000;
  background-color: #222;
  transition: margin-right 0.5s var(--mainTransition);
}

.contact-form-wrapper {
  margin: 10% 8%;
}

.contact-form-head {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  font-size: 2rem;
  letter-spacing: 0.1rem;
}

.contact-form-fields {
  margin: 10% 0;
}

.contact-form-input {
  border: none;
  border-radius: 1px;
  outline: none;
  background-color: #2f2f2f;
  padding: 12px 16px;
  color: var(--mainWhite);
}

.contact-form-button {
  border: 0.125rem solid var(--primaryColor);
  border-radius: 0;
  background: linear-gradient(to right, var(--primaryColor) 50%, transparent 50%);
	background-size: 200% 200%;
	background-position: right;
  transition: all 0.5s var(--mainTransition);
  outline: none;
  color: var(--mainWhite);
  padding: 8px 14px;
  margin: 12px 0;
}

.contact-form-input::-webkit-input-placeholder {
  color: var(--mainWhite);
}

.contact-form-input:-ms-input-placeholder {
  color: var(--mainWhite);
}

.contact-form-input::placeholder {
  color: var(--mainWhite);
}

.contact-form-button:hover {
  background-position: left;
}

.reachout-wrapper {
  margin: 2rem;
}

.contact-through-social-small {
  display: none;
}

@media only screen and (max-width: 1200px) {
  #contact-form {
    width: 50vw;
  }
}

@media only screen and (max-width: 800px) {
  #contact-form {
    width: 100vw;
  }
}

@media only screen and (max-width: 600px) {
  .contact-through-social {
    display: none;
  }

  .contact-through-social-small {
    display: block;
  }

  .contact-home {
    padding: 2% 7%;
  }
}
